body {
  background-image: linear-gradient(to right,#00acfe,#01fe6d);
}

#abcd {
  min-height: 0px;
  margin-top: 150px;
}

.outer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

textarea {
  text-align: center;
  align-items: center;
}

.inner {
  background-color: darkblue;
  width: fit-content;
  padding: 8px 8px;
  border-radius: 10px;
  height: 250px;
  width: 400px;
}

.outer>table {
  width: 100%;
}

table {
  height: 100%;
  width: 100%;
  border-spacing: 5px;
}

td {
  background-color: white;
  padding: 3px 3px;
  border-radius: 10px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

td:hover {
  background-color: violet;
  cursor: pointer;
  color: white;
}

#clear {
  background-color: green;
}

#clear:hover {
  color: white;
  background-color: violet;
}

#screen {
  background-color: grey;
  color: white;
  padding: 0 0;
}